@import '../../vars.scss';

.fixEmail {
  position: fixed;
  bottom: 0;
  right: 5%;
  z-index: 100;
  display: flex;
  align-items: center;
  gap: 2rem;
  writing-mode: vertical-rl;
  transform: translateX(-47.5%);

  &::after {
    content: "";
    height: 130px;
    width: 3px;
    border-radius: 50%;
    background-color: $color-accent;
    transform: translateX(-70%);
  }
}

.fixContacts {
  position: fixed;
  bottom: 0;
  left: 5%;
  z-index: 100;
  width: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  

  &::after {
    content: "";
    height: 130px;
    width: 3px;
    border-radius: 50%;
    background-color: $color-accent;
  }
}

.socialItem {
  transform: translateY(0) scale(1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    transform: translateY(-10%) scale(1.15);
  }
}

.socialIcons {
  width: 100%;
  height: 25px;
  fill: currentColor;
}